
.main-login{
    width: 100%;
    height: 100vh;
    background: #ebebeb;
    font-family: "Helvetica Neue","Hiragino Sans GB","Microsoft YaHei","\9ED1\4F53",Arial,sans-serif;
    color: #222;
    font-size: 12px;
}
*{padding: 0;margin: 0;
}
.top_div{
    background: #008ead;
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.top_div h1{
    color: #FFF;
    font-size: 30px;
    margin-top: -50px;
    text-shadow: black 0 1px 2px;
}
.ipt{
    border: 1px solid #d3d3d3;
    padding: 10px 10px;
    width: 340px;
    border-radius: 4px;
    padding-left: 35px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    -webkit-transition: border-color ease-in-out .3s,-webkit-box-shadow ease-in-out .3s;
    -o-transition: border-color ease-in-out .3s,box-shadow ease-in-out .3s;
    transition: border-color ease-in-out .3s,box-shadow ease-in-out .3s
}
.ipt:focus{
    border-color: #66afe9;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6)
}
.ipt.sm{
    width: 200px;
}
.u_logo{
    background: url("../../assets/images/login/username.png") no-repeat;
    padding: 10px 10px;
    position: absolute;
    top: 43px;
    left: 40px;
}
.p_logo{
    background: url("../../assets/images/login/password.png") no-repeat;
    padding: 10px 10px;
    position: absolute;
    top: 12px;
    left: 40px;
}
a{
    text-decoration: none;
}
.tou{
    background: url("../../assets/images/login/tou.png") no-repeat;
    width: 97px;
    height: 92px;
    position: absolute;
    top: -87px;
    left: 140px;
}
.left_hand{
    background: url("../../assets/images/login/left_hand.png") no-repeat;
    width: 32px;
    height: 37px;
    position: absolute;
    top: -38px;
    left: 150px;
    transition: top linear .3s, left linear .3s;
}
.right_hand{
    background: url("../../assets/images/login/right_hand.png") no-repeat;
    width: 32px;
    height: 37px;
    position: absolute;
    top: -38px;
    right: -64px;
    transition: top linear .3s, right linear .3s;
}
.initial_left_hand{
    background: url("../../assets/images/login/hand.png") no-repeat;
    width: 30px;
    height: 20px;
    position: absolute;
    top: -12px;
    left: 100px;
    transition: top linear .3s, left linear .3s;
}
.initial_right_hand{
    background: url("../../assets/images/login/hand.png") no-repeat;
    width: 30px;
    height: 20px;
    position: absolute;
    top: -12px;
    right: -112px;
    transition: top linear .3s, right linear .3s;
}
.left_handing{
    background: url("../../assets/images/login/left-handing.png") no-repeat;
    width: 30px;
    height: 20px;
    position: absolute;
    top: -24px;
    left: 139px;
}
.right_handinging{
    background: url("../../assets/images/login/right_handing.png") no-repeat;
    width: 30px;
    height: 20px;
    position: absolute;
    top: -21px;
    left: 210px;
}
.codeImg{
    width: 120px;
    height: 35px;
    float: right;
}
.codeImg img{
    width: 120px;
    height: 35px;
}



